/** 引入bootstrap样式 */
@import url("//cdn.bootcss.com/bootstrap/3.3.4/css/bootstrap.min.css");
/** 引入图标库 */
@import url("//at.alicdn.com/t/font_z5ckimosv54jkyb9.css");
/** 全局通用样式定义 */
/** 文字样式 **/
.text-gray {
  color: #D3DCE6;
}

/** 文字大小 **/
/** 动画特效 **/
.fade-transition-enter-active, .fade-transition-leave-active {
  transition: opacity .3s ease;
}
.fade-transition-enter, .fade-transition-leave-active {
  opacity: 0;
}
@keyframes dash {
from {
    stroke-dashoffset: 250;
    /* 这里是svg图形中素描线长度，可以使用js获取 */
}
to {
    stroke-dashoffset: 0;
}
}

/* 支持chrome浏览器 */
@-webkit-keyframes dash {
from {
    stroke-dashoffset: 250;
    /* 这里是svg图形中素描线长度，可以使用js获取 */
}
to {
    stroke-dashoffset: 0;
}
}
#content {
  background: #148bcd;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

/** absolute定位可以消除底部空白区 */
#footer {
  width: 100%;
  background: #fff;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
  position: absolute;
  bottom: 0;
}
#footer p {
    margin: 0;
    line-height: 40px;
}



